#menu-modal {
  position: absolute;
  top: 4.875rem;
  border: none;
  width: 100%;
  background-color: transparent;
  z-index: 1;
}

.menu-links {
  margin: 0;
  border-radius: 0.625rem;
  padding-block: 2.5rem;
  padding-inline: 1.5rem;
  list-style-type: none;
  background-color: var(--dk-violet);
}
.menu-links > *:not(:last-child) {
  margin-bottom: 2.125rem;
}
.menu-links li {
  font-size: 0.875rem;
  font-weight: 700;
  text-align: center;
}
.menu-links li hr {
  background-color: var(--gray);
}
.menu-links li a {
  width: 100%;
  color: var(--white);
  transition: all 200ms ease-in;
}
.menu-links li a:hover:not(.cta) {
  color: var(--cyan);
}
.menu-links li > .cta {
  padding-block: 1rem;
}

.cta {
  position: relative;
  display: inline-block;
  background-color: var(--cyan);
  border-radius: 1.875rem;
  font-weight: 700;
  color: var(--white);
}
.cta::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  height: 100%;
  display: block;
  border-radius: inherit;
  background-color: var(--white);
  transition: opacity 300ms ease-in;
  opacity: 0;
}
.cta:hover::before {
  opacity: 0.5;
}

.shorten {
  display: flex;
  flex-flow: column nowrap;
  gap: 1rem;
  border-radius: 0.625rem;
  padding: 1.5rem;
  background-color: var(--dk-violet);
  background-image: url("../images/bg-shorten-mobile.svg");
  background-repeat: no-repeat;
  background-size: 75% 80%;
  background-position: top right;
}
@media (min-width: 43.75em) {
  .shorten {
    flex-flow: row wrap;
    gap: 1.4375rem;
    padding-block: 3.125rem;
    padding-inline: 4rem;
    background-image: url("../images/bg-shorten-desktop.svg");
    background-size: 100% 100%;
    background-position: center center;
  }
}
@media (max-width: 87.44em) {
  .shorten__error {
    gap: 2.25rem;
  }
}
.shorten .input-label {
  position: relative;
}
@media (min-width: 43.75em) {
  .shorten .input-label {
    flex-grow: 4;
  }
}
.shorten .input-label span {
  position: absolute;
  bottom: -35%;
  display: block;
  font-size: 0.6875rem;
  font-style: italic;
  color: var(--red);
}
@media (min-width: 43.75em) {
  .shorten .input-label span {
    font-size: 0.8125rem;
  }
}
.shorten__input {
  width: 100%;
  padding: 1.125rem;
  border: none;
  border-radius: 0.3125rem;
  font-size: 0.8125rem;
  font-weight: 500;
  font-family: var(--font);
  color: var(--very-dk-blue);
}
@media (min-width: 43.75em) {
  .shorten__input {
    flex-grow: 4;
    padding-block: 1.28125rem;
    padding-inline: 2rem;
    font-size: 0.9375rem;
  }
}
.shorten__input--error {
  border: 0.1875rem solid var(--red);
  color: var(--red);
}
.shorten__submit {
  border: none;
  border-radius: 0.3125rem;
  padding-block: 1rem;
  font-size: 0.9375rem;
  font-weight: 700;
  font-family: var(--font);
  color: var(--white);
  background-color: var(--cyan);
  cursor: pointer;
}
@media (min-width: 43.75em) {
  .shorten__submit {
    flex-grow: 1;
    font-size: 1.0625rem;
  }
}

.footer-links {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  gap: 3rem;
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: center;
  font-size: 0.75rem;
  font-weight: 700;
  color: var(--white);
}
@media (min-width: 43.75em) {
  .footer-links {
    grid-template-columns: repeat(2, 1fr);
    justify-content: space-between;
    font-size: 0.875rem;
  }
}
@media (min-width: 71.875em) {
  .footer-links {
    grid-template-columns: repeat(4, 1fr);
    justify-content: space-between;
    gap: 0;
    text-align: left;
  }
}
.footer-links > li:not(:last-child) span {
  display: block;
  margin-bottom: 1.875rem;
}
.footer-links > li:not(:last-child) ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.footer-links > li:not(:last-child) ul > *:not(:last-child) {
  margin-bottom: 1.1875rem;
}
.footer-links > li:not(:last-child) ul li {
  font-size: 0.875rem;
}
@media (min-width: 71.875em) {
  .footer-links > li:not(:last-child) ul li {
    font-size: 1rem;
  }
}
.footer-links > li:not(:last-child) ul li a {
  color: var(--gray-violet);
  transition: all 200ms ease-in;
}
.footer-links > li:not(:last-child) ul li a:hover {
  color: var(--cyan);
}

.socmed-links {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  gap: 1.5rem;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.socmed-links li {
  position: relative;
  width: 1.5rem;
  height: 1.5rem;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
  cursor: pointer;
}
.socmed-links li::before {
  content: "";
  position: absolute;
  top: 0;
  display: block;
  width: inherit;
  height: inherit;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
  opacity: 0;
  transition: opacity 300ms ease-in;
}
.socmed-links li:hover::before {
  opacity: 1;
}
.socmed-links li a {
  display: block;
  width: inherit;
  height: inherit;
  font-size: 0;
}
.socmed-links__fb {
  background-image: url("../images/icon-facebook.svg");
}
.socmed-links__fb::before {
  background-image: url("../images/icon-facebook-hover.svg");
}
.socmed-links__twitter {
  background-image: url("../images/icon-twitter.svg");
}
.socmed-links__twitter::before {
  background-image: url("../images/icon-twitter-hover.svg");
}
.socmed-links__pinterest {
  background-image: url("../images/icon-pinterest.svg");
}
.socmed-links__pinterest::before {
  background-image: url("../images/icon-pinterest-hover.svg");
}
.socmed-links__insta {
  background-image: url("../images/icon-instagram.svg");
}
.socmed-links__insta::before {
  background-image: url("../images/icon-instagram-hover.svg");
}

html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: var(--font);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  overflow-x: hidden;
  padding: 0;
  margin: 0;
  font-size: 1.125rem;
  font-weight: 500;
  color: var(--gray);
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --cyan: hsl(180, 66%, 49%);
  --dk-violet: hsl(257, 27%, 26%);
  --red: hsl(0, 87%, 67%);
  --gray: hsl(0, 0%, 75%);
  --gray-violet: hsl(257, 7%, 63%);
  --very-dk-blue: hsl(255, 11%, 22%);
  --very-dk-violet: hsl(260, 8%, 14%);
}

:root {
  --font: "Poppins", sans-serif;
}

.container {
  max-width: 69.25rem;
  margin: 0 auto;
  padding-inline: 1.5rem;
}
@media (min-width: 71.875em) {
  .container {
    padding-inline: 0;
  }
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.1;
  font-weight: 700;
}

p {
  margin: 0;
}

a:link,
a:visited,
a:hover {
  text-decoration: none;
}

.nav {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  padding-top: 2.5rem;
  padding-bottom: 1.375rem;
}
@media (min-width: 43.75em) {
  .nav {
    padding-top: 3rem;
    padding-bottom: 4.8125rem;
    gap: 2.8125rem;
  }
}
.nav img {
  width: 7.5rem;
  height: 2.0625rem;
  cursor: pointer;
}
.nav div {
  display: flex;
  flex-flow: column wrap;
  justify-content: space-between;
  width: 1.5rem;
  height: 1.3125rem;
  cursor: pointer;
}
@media (min-width: 43.75em) {
  .nav div {
    display: none;
  }
}
.nav div span {
  display: block;
  height: 0.1875rem;
  width: 100%;
  background-color: var(--gray-violet);
}
.nav__links {
  flex-grow: 1;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  gap: 1.875rem;
  margin: 0;
  padding: 0;
  list-style-type: none;
  font-size: 0.875rem;
  font-weight: 700;
}
@media (max-width: 43.7em) {
  .nav__links {
    display: none;
  }
}
.nav__links li a:not(.cta) {
  color: var(--gray-violet);
  transition: all 300ms ease-in;
}
.nav__links li a:not(.cta):hover {
  color: var(--cyan);
}
.nav__links li > .cta {
  padding-block: 1rem;
}
@media (min-width: 43.75em) {
  .nav__links li > .cta {
    padding-block: 0.625rem;
    padding-inline: 1.5625rem;
  }
}
.nav__links .separator {
  flex-grow: 1;
}

.hero {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
  gap: 2.8125rem;
  margin-bottom: 10.5rem;
}
@media (min-width: 43.75em) {
  .hero {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: none;
  }
}
@media (min-width: 71.875em) {
  .hero {
    gap: 6.875rem;
  }
}
.hero__image {
  position: relative;
  display: block;
  width: 100%;
  height: 21.1875rem;
}
@media (min-width: 43.75em) {
  .hero__image {
    height: 30.1875rem;
    grid-column: 2/3;
    grid-row: 1/2;
  }
}
.hero__image::before {
  content: "";
  display: block;
  height: inherit;
  position: absolute;
  top: 0;
  right: -40%;
  bottom: 0;
  left: 0%;
  background-image: url("../images/illustration-working.svg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center center;
}
@media (min-width: 43.75em) {
  .hero__image::before {
    right: -65%;
  }
}
.hero__content {
  text-align: center;
}
@media (min-width: 43.75em) {
  .hero__content {
    place-self: center;
    text-align: left;
  }
}
@media (min-width: 43.75em) {
  .hero__content {
    grid-column: 1/2;
    grid-row: 1/2;
  }
}
.hero__content h1 {
  margin-bottom: 1.6875rem;
  font-size: 2.125rem;
  color: var(--very-dk-blue);
}
@media (min-width: 43.75em) {
  .hero__content h1 {
    margin-bottom: 1.5rem;
    font-size: 3.6875rem;
  }
}
.hero__content p {
  margin-bottom: 2.25rem;
  color: var(--gray-violet);
}
@media (min-width: 43.75em) {
  .hero__content p {
    margin-bottom: 2.75rem;
  }
}
.hero__content a {
  padding-block: 1rem;
  padding-inline: 2.5rem;
  font-size: 1rem;
}
@media (min-width: 43.75em) {
  .hero__content a {
    padding-inline: 3.4375rem;
  }
}

.features {
  position: relative;
  padding-block: 5rem;
  background-color: hsl(0deg, 0%, 90%);
}
.features__shorten {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}
.features__info {
  padding-top: 5.9375rem;
  text-align: center;
}
.features__info h2 {
  margin-bottom: 2.1875rem;
  font-size: 1.4375rem;
  color: var(--very-dk-blue);
}
@media (min-width: 43.75em) {
  .features__info h2 {
    margin-bottom: 2.0625rem;
    font-size: 2.0625rem;
  }
}
.features__info p {
  margin-bottom: 6.0625rem;
  padding-inline: 1.25rem;
  font-size: 0.875rem;
  line-height: 2;
  color: var(--gray-violet);
}
@media (min-width: 71.875em) {
  .features__info p {
    margin-bottom: 6.5625rem;
    padding-inline: 20.625rem;
    font-size: 0.9375rem;
  }
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: 1fr auto 1fr auto 1fr;
    height: 22.5rem;
  }
}
.cards__section {
  position: relative;
  width: 100%;
  border-radius: 0.3125rem;
  padding-top: 5.25rem;
  padding-inline: 2.25rem;
  padding-bottom: 2.875rem;
  background-color: var(--white);
}
@media (min-width: 71.875em) {
  .cards__section {
    text-align: left;
  }
}
.cards__section div {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 5.5rem;
  height: 5.5rem;
  border-radius: 50%;
  background-color: var(--dk-violet);
  background-repeat: no-repeat;
  background-position: center center;
}
@media (min-width: 71.875em) {
  .cards__section div {
    left: 10%;
    transform: translate(0, -50%);
  }
}
.cards__section h3 {
  margin-bottom: 1.8125rem;
  font-size: 1.125rem;
  color: var(--very-dk-violet);
}
.cards__section p {
  margin: 0;
  padding: 0;
  font-size: 0.75rem;
}
.cards__section--brandicon {
  background-image: url("/images/icon-brand-recognition.svg");
  background-size: 2.5625rem 2.5625rem;
}
.cards__section--recordicon {
  background-image: url("/images/icon-detailed-records.svg");
  background-size: 2.5rem 2.5rem;
}
.cards__section--customicon {
  background-image: url("/images/icon-fully-customizable.svg");
  background-size: 3rem 3rem;
}
.cards .top {
  place-self: start;
}
.cards .center {
  place-self: center;
}
.cards .bottom {
  place-self: end;
}
.cards span {
  display: block;
  height: 5.625rem;
  width: 0.5rem;
  margin-inline: auto;
  background-color: var(--cyan);
}
@media (min-width: 71.875em) {
  .cards span {
    place-self: center;
    height: 0.5rem;
    width: 1.75rem;
    margin: 0;
  }
}

.footer__section1 {
  padding-block: 6rem;
  text-align: center;
  background-color: var(--dk-violet);
  background-image: url("../images/bg-boost-mobile.svg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center center;
}
@media (min-width: 43.75em) {
  .footer__section1 {
    padding-block: 4.4375rem;
    background-image: url("../images/bg-boost-desktop.svg");
  }
}
.footer__section1 h2 {
  margin-bottom: 1.6875rem;
  font-size: 1.4375rem;
  color: var(--white);
}
@media (min-width: 43.75em) {
  .footer__section1 h2 {
    font-size: 2.0625rem;
  }
}
.footer__section1 a {
  padding-block: 1rem;
  padding-inline: 3.25rem;
  font-size: 1rem;
}
.footer > div {
  background-color: var(--very-dk-violet);
}
.footer__section2 {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  padding-block: 3.375rem;
}
@media (min-width: 71.875em) {
  .footer__section2 {
    grid-template-columns: 1fr 2fr;
    padding-block: 4.625rem;
  }
}
.footer__section2 img {
  place-self: center;
  width: 7.5rem;
  height: 2.1875rem;
  margin-bottom: 3.3125rem;
}
@media (min-width: 71.875em) {
  .footer__section2 img {
    place-self: start;
  }
}